import { Editor } from "@tiptap/react";
import { Tooltip, TooltipContent, TooltipProvider, TooltipTrigger } from "../ui/tooltip";
import { Toggle } from "../ui/toggle";
import { AlignCenter, AlignLeft, AlignRight, Bold, Heading1Icon, Heading2Icon, Heading3Icon, Italic, ListCheckIcon, ListOrderedIcon, Strikethrough } from "lucide-react";
import { cn } from "@/lib/utils";
import { useState, useEffect } from "react";
// import TextAlign from "@tiptap/extension-text-align"

interface iAppProps {
  editor: Editor | null;
}

export function Menubar({ editor }: iAppProps) {
  // State to force re-render
  const [, setRender] = useState({});

  // Subscribe to editor changes and trigger re-render
  useEffect(() => {
    if (!editor) return;

    const update = () => setRender({});

    editor.on("transaction", update);

    return () => {
      editor.off("transaction", update);
    };
  }, [editor]);

  if (!editor) {
    return null;
  }

  return (
    <div className="border border-input rounded-t-lg p-2 bg-card flex flex-wrap gap-1 items-center">
      <TooltipProvider>
        <div className="flex flex-wrap gap-1">
          <Tooltip>
            <TooltipTrigger asChild>
              <Toggle
                size="sm"
                pressed={editor.isActive("bold")}
                onPressedChange={() => editor.chain().focus().toggleBold().run()}
                className={cn(
                  editor.isActive("bold") && "bg-muted text-muted-foreground"
                )}
              >
                <Bold />
              </Toggle>
            </TooltipTrigger>
            <TooltipContent>Bold</TooltipContent>
          </Tooltip>

          <Tooltip>
            <TooltipTrigger asChild>
              <Toggle
                size="sm"
                pressed={editor.isActive("italic")}
                onPressedChange={() => editor.chain().focus().toggleItalic().run()}
                className={cn(
                  editor.isActive("italic") && "bg-muted text-muted-foreground"
                )}
              >
                <Italic />
              </Toggle>
            </TooltipTrigger>
            <TooltipContent>Italic</TooltipContent>
          </Tooltip>

           <Tooltip>
            <TooltipTrigger asChild>
              <Toggle
                size="sm"
                pressed={editor.isActive("strike")}
                onPressedChange={() => editor.chain().focus().toggleStrike().run()}
                className={cn(
                  editor.isActive("strike") && "bg-muted text-muted-foreground"
                )}
              >
                <Strikethrough />
              </Toggle>
            </TooltipTrigger>
            <TooltipContent>Strike</TooltipContent>
          </Tooltip>

           <Tooltip>
            <TooltipTrigger asChild>
              <Toggle
                size="sm"
                pressed={editor.isActive("heading", {level: 1})}
                onPressedChange={() => editor.chain().focus().toggleHeading({ level: 1}).run()}
                className={cn(
                 editor.isActive("heading", {level: 1}) && "bg-muted text-muted-foreground"
                )}
              >
                <Heading1Icon />
              </Toggle>
            </TooltipTrigger>
            <TooltipContent>Heading 1</TooltipContent>
          </Tooltip>

           <Tooltip>
            <TooltipTrigger asChild>
              <Toggle
                size="sm"
                pressed={editor.isActive("heading", {level: 2})}
                onPressedChange={() => editor.chain().focus().toggleHeading({ level: 2}).run()}
                className={cn(
                 editor.isActive("heading", {level: 2}) && "bg-muted text-muted-foreground"
                )}
              >
                <Heading2Icon />
              </Toggle>
            </TooltipTrigger>
            <TooltipContent>Heading 2</TooltipContent>
          </Tooltip>

          <Tooltip>
            <TooltipTrigger asChild>
              <Toggle
                size="sm"
                pressed={editor.isActive("heading", {level: 3})}
                onPressedChange={() => editor.chain().focus().toggleHeading({ level: 3}).run()}
                className={cn(
                 editor.isActive("heading", {level: 3}) && "bg-muted text-muted-foreground"
                )}
              >
                <Heading3Icon />
              </Toggle>
            </TooltipTrigger>
            <TooltipContent>Heading 3</TooltipContent>
          </Tooltip>

          <Tooltip>
            <TooltipTrigger asChild>
              <Toggle
                size="sm"
                pressed={editor.isActive("bulletList")}
                onPressedChange={() => editor.chain().focus().toggleBulletList().run()}
                className={cn(
                 editor.isActive("bulletList") && "bg-muted text-muted-foreground"
                )}
              >
                <ListCheckIcon/>
              </Toggle>
            </TooltipTrigger>
            <TooltipContent>Bullet List</TooltipContent>
          </Tooltip>

           <Tooltip>
            <TooltipTrigger asChild>
              <Toggle
                size="sm"
                pressed={editor.isActive("orderedList")}
                onPressedChange={() => editor.chain().focus().toggleOrderedList().run()}
                className={cn(
                 editor.isActive("orderedList") && "bg-muted text-muted-foreground"
                )}
              >
                <ListOrderedIcon/>
              </Toggle>
            </TooltipTrigger>
            <TooltipContent>Ordered List</TooltipContent>
          </Tooltip>
        </div>

        <div className="w-px h-6 bg-border mx-2">
            
        <div className="flex flex-wrap gap-1">
        <Tooltip>
        <TooltipTrigger asChild>
        <Toggle
            size="sm"
            pressed={editor.isActive({textAlign: "left"})}
            onPressedChange={() => editor.chain().focus().setTextAlign("left").run()}
            className={cn(
            editor.isActive({textAlign: "left"}) && "bg-muted text-muted-foreground"
            )}
        >
            <AlignLeft/>
        </Toggle>
        </TooltipTrigger>
        <TooltipContent>Align Left</TooltipContent>
        </Tooltip>

         <Tooltip>
        <TooltipTrigger asChild>
        <Toggle
            size="sm"
            pressed={editor.isActive({textAlign: "center"})}
            onPressedChange={() => editor.chain().focus().setTextAlign("center").run()}
            className={cn(
            editor.isActive({textAlign: "center"}) && "bg-muted text-muted-foreground"
            )}
        >
            <AlignCenter/>
        </Toggle>
        </TooltipTrigger>
        <TooltipContent>Align Center</TooltipContent>
        </Tooltip>

        <Tooltip>
        <TooltipTrigger asChild>
        <Toggle
            size="sm"
            pressed={editor.isActive({textAlign: "right"})}
            onPressedChange={() => editor.chain().focus().setTextAlign("right").run()}
            className={cn(
            editor.isActive({textAlign: "right"}) && "bg-muted text-muted-foreground"
            )}
        >
            <AlignRight/>
        </Toggle>
        </TooltipTrigger>
        <TooltipContent>Align Right</TooltipContent>
        </Tooltip>
        </div>
        </div>
      </TooltipProvider>
    </div>
  );
}
